*,
*::before,
*::after {
  box-sizing: border-box;
}

html,
body,
div,
span,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
abbr,
address,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
samp,
small,
strong,
sub,
sup,
var,
b,
i,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  outline: 0;
  font-size: 100%;
  vertical-align: baseline;
  background: transparent;
}

body {
  line-height: 1;
}

article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}

nav ul {
  list-style: none;
}

blockquote,
q {
  quotes: none;
}

blockquote:before,
blockquote:after,
q:before,
q:after {
  content: "";
  content: none;
}

a {
  margin: 0;
  padding: 0;
  font-size: 100%;
  vertical-align: baseline;
  background: transparent;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

input,
select {
  vertical-align: middle;
}

@font-face {
  font-family: "Leeague Spartan Bold";
  src: url("../fonts/leaguespartan-bold-webfont.woff2") format("woff2"), url("../fonts/leaguespartan-bold-webfont.woff") format("woff");
  font-weight: normal;
  font-style: normal;
}
/* BASE */
button {
  padding: 0;
  margin: 0;
}

button, a.btn-as-link {
  border-style: none;
  border-radius: 4px;
  padding: 10px 16px;
  background-color: #F2C135;
  color: #314352;
  font-family: "Leeague Spartan Bold";
  font-size: 18px;
  cursor: pointer;
  transition: 0.4s;
  box-shadow: 6.7px 6.7px 5.3px rgba(0, 0, 0, 0.028), 22.3px 22.3px 17.9px rgba(0, 0, 0, 0.042), 0px 100px 80px rgba(0, 0, 0, 0.07);
}
button:hover, a.btn-as-link:hover {
  color: #495659;
  background-color: #FDE079;
}

::-webkit-scrollbar {
  width: 7px;
}

::-webkit-scrollbar-track {
  background-color: #314352;
}

::-webkit-scrollbar-thumb {
  background: #F2C135;
  border-radius: 1px;
  border-left: solid 7px #F2C135;
}

::selection {
  background-color: #F2C135;
}

body {
  background-color: #F5F7F7;
  font-family: "Leeague Spartan Bold";
  color: #314352;
  padding-bottom: 180px;
  background-color: #F5F7F7;
  background-image: linear-gradient(#E4E6E8 1px, transparent 1px), linear-gradient(90deg, #E4E6E8 1px, transparent 1px), linear-gradient(#E4E6E8 0.5px, transparent 0.5px), linear-gradient(90deg, #E4E6E8 0.5px, #F5F7F7 0.5px);
  background-size: 25px 25px, 25px 25px, 5px 5px, 5px 5px;
  background-position: -1px -1px, -1px -1px, -0.5px -0.5px, -0.5px -0.5px;
}

img {
  max-width: 100%;
}

a {
  color: #314352;
  text-decoration: none;
}
a.text-link {
  position: relative;
  transition: 0.4s;
}
a.text-link:before {
  position: absolute;
  z-index: -1;
  bottom: 1px;
  width: 100%;
  content: "";
  border-bottom: solid 10px #F2C135;
  transition: 0.4s;
}
a.text-link:hover {
  color: #495659;
}
a.text-link:hover:before {
  border-bottom: solid 4px #FDE079;
}

p, span {
  font-size: 18px;
  line-height: 30px;
}

/* HEADER */
.custom-shape-divider-top-1643150368 {
  position: fixed;
  z-index: -1;
  top: 0;
  left: 0;
  width: 100%;
  overflow: hidden;
  line-height: 0;
}

.custom-shape-divider-top-1643150368 svg {
  position: relative;
  display: block;
  width: calc(300% + 1.3px);
  height: 280px;
}

.custom-shape-divider-top-1643150368 .shape-fill {
  fill: #F2C135;
}

.about-me {
  display: grid;
  place-items: center;
}
.about-me .greeting {
  margin: 30px 0;
  font-size: 40px;
  line-height: 48px;
}
@media (max-width: 991.98px) {
  .about-me .greeting {
    margin: 30px;
  }
}
.about-me .avatar {
  max-width: 220px;
  margin: 10px 0;
}
.about-me .avatar img {
  border-radius: 50%;
}
.about-me .profession {
  margin: 30px 0;
  font-size: 24px;
  line-height: 35px;
}
@media (max-width: 991.98px) {
  .about-me .profession {
    margin: 30px;
  }
}
@media (max-width: 767.98px) {
  .about-me .btn-resume {
    place-self: self-start;
    margin: 0 30px;
  }
}

/* MAIN */
.details-about-me,
.recent-works {
  max-width: 800px;
  margin: 0 auto;
}
@media (max-width: 991.98px) {
  .details-about-me,
.recent-works {
    margin: 0 30px;
  }
}

.details-about-me p {
  padding: 50px 0;
}

.recent-works {
  background-color: #FFF;
  padding: 30px;
  position: relative;
  z-index: 0;
  border-radius: 4px;
  box-shadow: 12.5px 12.5px 10px rgba(0, 0, 0, 0.04), 0px 100px 80px rgba(0, 0, 0, 0.07);
}
@media (max-width: 991.98px) {
  .recent-works {
    padding: 30px 0;
  }
}
@media (max-width: 991.98px) {
  .recent-works > * {
    margin-left: 30px;
    margin-right: 30px;
  }
}
.recent-works > h3 {
  font-size: 18px;
}
.recent-works > p:not(:last-child) {
  margin-top: 15px;
  margin-bottom: 15px;
}

/* FOOTER */
.nav-social {
  position: fixed;
  bottom: 0;
  left: 0;
  height: 57px;
  width: 100%;
  background-color: #314352;
}
@media (max-width: 767.98px) {
  .nav-social {
    height: 45px;
  }
}
.nav-social ul {
  display: flex;
  height: 100%;
  max-width: 550px;
  margin: 0 auto;
}
.nav-social ul li {
  flex-grow: 1;
  display: flex;
  align-items: center;
  justify-content: center;
}
.nav-social ul li a {
  color: #F2C135;
  font-size: 28px;
}
@media (max-width: 767.98px) {
  .nav-social ul li a {
    font-size: 20px;
  }
}